export class PopProvider extends Component {
	state = {
		products: [
			{
				id: 1,
				name: "tequeno"
			}
		],
		userSelected: null,
		clientName: "",
		amount: 6,
		order: "tequenio",
		clients: [
			{
				name: "Javier",
				id: 0,
				order: [
					{
						item: "tequeno",
						itemId: 1,
						quantity: 5
					},
					{
						item: "pastelpapa", // no se si el nombre aqui vaya
						itemId: 2,
						quantity: 5
					}
				]
			}
		]
	};

	setUserName = () => {
		console.log(this.state.userSelected);
		const clients = this.state.clients;
		const clientName = clients.filter(
			client => this.state.userSelected === client.id
		);
		console.log(clientName);
		this.setState({ clientName });
	};

	addUser = user => {
		const newClients = Array.from(this.state.clients);
		const newClient = {
			name: user,
			id: uniqueId()
		};
		newClients.push(newClient);
		newClients.sort((a, b) => (a.id > b.id ? 1 : -1));

		this.setState({ clients: newClients.reverse() }, () => {
			// console.log(this.state.clients);
		});
	};

	setUser = id => {
		this.setState({
			userSelected: id
		});
		this.setUserName();
	};

	render() {
		return (
			<PopContext.Provider
				value={{
					state: this.state,
					// increaseOrder: () => this.setState({ amount: this.state.amount + 1 }),
					//increaseItem: (type) => this.setState({ type: this.state.type + 1 }), // interesting...
					addUser: this.addUser,
					setUser: this.setUser
				}}
			>
				{this.props.children}
			</PopContext.Provider>
		);
	}
}

